import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index/index'
import login from '@/components/login/login'

Vue.use(Router)

export default new Router({
  routes: [
    {path: '/', component: index},
    {path: '/login', component: login},
    {path: '/hospitalMenu', component: require('@/components/menu/hospitalMenu')},
    {path: '/expertMenu', component: require('@/components/menu/expertMenu')},
    {path: '/allocatingMenu', component: require('@/components/menu/allocatingMenu')},
    {path: '/releaseMenu', component: require('@/components/menu/releaseMenu')},
    {path: '/manageMenu', component: require('@/components/menu/manageMenu')},
    {path: '/outlayMenu', component: require('@/components/menu/outlayMenu')},
    {
      path: '/hospital',//
      component: require('@/components/hospital/hospital'),
      children: [
        {path: '/hospital',                   component: require('@/components/hospital/children/routine/addRecord')},           //
        {path: '/hospital/addRecord',         component: require('@/components/hospital/children/routine/addRecord')},           //---
        {path: '/hospital/pendingRecord',     component: require('@/components/hospital/children/routine/pendingRecord')},       //---
        {path: '/hospital/alreadyRecord',     component: require('@/components/hospital/children/routine/alreadyRecord')},       //---
        {path: '/hospital/retreatRecord',     component: require('@/components/hospital/children/routine/retreatRecord')},       //---

        {path: '/hospital/addConsult',        component: require('@/components/hospital/children/consult/addConsult')},          //---
        {path: '/hospital/InProcessConsult',  component: require('@/components/hospital/children/consult/InProcessConsult')},    //---
        {path: '/hospital/alreadyConsult',    component: require('@/components/hospital/children/consult/alreadyConsult')},      //---

        {path: '/hospital/pendingYYFrozen',   component: require('@/components/hospital/children/frozen/pendingYYFrozen')},      //---
        {path: '/hospital/pendingCLFrozen',   component: require('@/components/hospital/children/frozen/pendingCLFrozen')},      //---
        {path: '/hospital/alreadyHandle',     component: require('@/components/hospital/children/frozen/alreadyHandle')},        //---

        {path: '/hospital/statistics',        component: require('@/components/hospital/children/statistics/statistics')},       //---
        {path: '/hospital/query',             component: require('@/components/hospital/children/statistics/query')},            //---

        {path: '/hospital/templateMaintain',  component: require('@/components/hospital/children/templateMaintain/templateMaintain')},//---
      ]
    },
    {
      path: '/expert',//
      component: require('@/components/expert/expert'),
      children: [
        {path: '/expert',                    component: require('@/components/expert/children/schedule/schedule')},              //
        {path: '/expert/schedule',           component: require('@/components/expert/children/schedule/schedule')},              //--

        {path: '/expert/stayDiagnosis',      component: require('@/components/expert/children/routine/stayDiagnosis')},          //--
        {path: '/expert/alreadyDiagnosis',   component: require('@/components/expert/children/routine/alreadyDiagnosis')},       //--

        {path: '/expert/beingProcessed',     component: require('@/components/expert/children/consult/beingProcessed')},         //--
        {path: '/expert/alreadyHandleConsult',component: require('@/components/expert/children/consult/alreadyHandle')},          //--

        {path: '/expert/frozenOrder',        component: require('@/components/expert/children/frozen/frozenOrder')},             //--
        {path: '/expert/stayHandle',         component: require('@/components/expert/children/frozen/stayHandle')},              //---
        {path: '/expert/alreadyHandleFrozen',component: require('@/components/expert/children/frozen/alreadyHandle')},           //---

        {path: '/expert/statistics',         component: require('@/components/expert/children/statistics/statistics')},          //---
        {path: '/expert/query',              component: require('@/components/expert/children/statistics/query')},               //---
      ]
    },
    {
      path: '/allocating',//
      component: require('@/components/allocating/allocating')
    },
    {
      path: '/release',//
      component: require('@/components/release/release')
    },
    {
      path: '/manage',//
      component: require('@/components/manage/manage')
    },
    {
      path: '/outlay',//
      component: require('@/components/outlay/outlay')
    }
  ]
})



