<style lang="less">
@import "../../css/layout";
</style>
<template>
  <div class="expert layout">
    <div class="layout-header">
      <h1 @click="toMenu">LOGO</h1>
      <div class="user">
        <div class="user-info">
          <el-dropdown trigger="click"  @command="handleCommand">
            <span class="el-dropdown-link">
              <i class="el-icon-caret-bottom el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command=""></el-dropdown-item>
              <el-dropdown-item command=""></el-dropdown-item>
              <el-dropdown-item command=""></el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
    </div>
    <div class="layout-main">
      <div class="layout-main-nav">
        <pt-nav-view :navdata="navInfo"></pt-nav-view>
      </div>
      <div class="layout-main-info">
        <router-view></router-view>
      </div>
    </div>
    <div class="copyright">
      Copyright 2006-2017 91360.com All Rights Reserved.40004-91360
      <br>
      201541 | ICP13032250 | B2-20160359 |
      <img class="copyright-img" src="http://style.91360.com/Images/.png" alt="">
       32011402010112
    </div>
  </div>
</template>
<script>
  import ptNavData from '../../api/pt-nav.js'
  import ptNavView from '../public/pt-nav.vue'
  export default {
    name: 'expert',
    data(){
      return {
        navInfo: ptNavData.expert,
      }
    },
    components: {
      ptNavView
    },
    methods: {
      toMenu(){
        this.$router.push({path: '/expertMenu'})
      },
      handleCommand(command) {
        if(command == ""){
          this.$router.push({path: '/'})
        }else {
          this.$message('' + command + '');
        }
      }
    }
  }
</script>



