      this.showRecordNumber(0);
    },
    methods: {
      showRecordNumber(i){
        var myEchart = echarts.init(document.getElementById('echarsEl'));
        var opt = {};
        this.act = i
        if(i == 0){
          opt = this.recordNumber;
        }else if(i == 1){
            opt = this.sectionQuality;
        }else if(i == 2){
          opt = this.timelyRate;
        }else if(i == 3){
            opt = this.delayRate;
        }else {
            opt = this.Cost
        }
        myEchart.setOption(opt,true)
      }
    }
